@import "../utils/_variables.scss";
@import "../utils/_functions.scss";

@each $theme-name, $theme in $themes {
    body[theme="#{$theme-name}"] .TextEditor {
        .RichTextEditor__root___2QXK- {
            position: relative;
            max-height: 30em;
            overflow-y: auto;
            border: none;

            .EditorToolbar__root___3_Aqz {
                position: sticky;
                top: 0;
                background-color: map-deep-get($theme, "secondary", "light");
                z-index: 2;
            }
        }

        &-content, .public-DraftEditor-content {
            font-family: "Roboto","Helvetica","Arial",sans-serif;
            font-weight: 400;
            font-size: 1.15em;
            line-height: 1.5;
            letter-spacing: 0.00938em;
            color: rgba(0, 0, 0, 0.6);
            margin-top: 16px;

            li {
                & + li {
                    margin-top: 0.25em;
                }
            }

            h1 {
                font-size: 2em;
                margin-bottom: 0.5em;
                &:not(:first-child) {
                    margin-top: 1em;
                }
            }

            h2 {
                font-size: 1.75em;
                margin-bottom: 0.5714em;

                &:not(:first-child) {
                    margin-top: 1.1428em;
                }
            }

            h3 {
                font-size: 1.5em;
                margin-bottom: 0.6666em;

                &:not(:first-child) {
                    margin-top: 1.3333em;
                }
            }

            h4 {
                font-size: 1.25em;
                margin-bottom: 0.8em;
            }

            h5 {
                font-size: 1.125em;
                margin-bottom: 0.8888em;
            }

            h6 {
                font-size: 1em;
                margin-bottom: 1em;
            }

            blockquote {
                background-color: #f5f5f5;
                border-left: 5px solid #dbdbdb;
                padding: 1.25em 1.5em;
            }

            ol {
                list-style-position: outside;
                margin-left: 2em;
                margin-top: 1em;

                &:not([type]) {
                    list-style-type: decimal;
                }
            }

            ul {
                list-style: disc outside;
                margin-left: 2em;
                margin-top: 1em;

                ul {
                    list-style-type: circle;
                    margin-top: 0.5em;

                    ul {
                        list-style-type: square;
                    }
                }
            }

            dd {
                margin-left: 2em;
            }

            figure {
                margin-left: 2em;
                margin-right: 2em;
                text-align: center;

                &:not(:first-child) {
                    margin-top: 2em;
                }
                &:not(:last-child) {
                    margin-bottom: 2em;
                }

                img {
                    display: inline-block;
                }

                figcaption {
                    font-style: italic;
                }
            }

            pre {
                -webkit-overflow-scrolling: touch;
                overflow-x: auto;
                padding: 1.25em 1.5em;
                white-space: pre;
                word-wrap: normal;
            }

            table {
                width: 100%;

                th {
                    color: #363636;

                    &:not([align]) {
                        text-align: inherit;
                    }
                }
            }

            blockquote:not(:last-child),
            dl:not(:last-child),
            ol:not(:last-child),
            p:not(:last-child),
            pre:not(:last-child),
            table:not(:last-child),
            ul:not(:last-child) {
                margin-bottom: 1em;
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: #363636;
                font-weight: 600;
                line-height: 1.125;
            }

            sub,
            sup {
                font-size: 75%;
            }

            table {
                td,
                th {
                    border: 1px solid #dbdbdb;
                    border-width: 0 0 1px;
                    padding: 0.5em 0.75em;
                    vertical-align: top;
                }

                thead {
                    th,
                    td {
                        border-width: 0 0 2px;
                        color: #363636;
                    }
                }

                tfoot {
                    td,
                    th {
                        border-width: 2px 0 0;
                        color: #363636;
                    }
                }

                tbody {
                    tr:last-child td,
                    tr:last-child th {
                        border-bottom-width: 0;
                    }
                }
            }
        }
    }
}